import React, { ReactNode } from 'react';
import './Button.css';

interface ButtonProps {
    variant?: 'primary' | 'secondary' | 'tertiary'; 
    primary?: boolean; 
    size?: 'small' | 'medium' | 'large';
    children: ReactNode;
    disabled?: boolean;
    onClick?: () => void;
}


export const Button = ({
    variant='primary',
    primary = false,
    size = 'medium',
    disabled = false,
    
    children,
    ...props
}: ButtonProps) => {
    //   const mode = primary ? 'button--primary' : 'button--secondary';

    if(disabled) {

    }

    return (
    <button
      type="button"
      disabled={disabled}
      className={
        ['button', `button--${size}`, `button--${variant}`].join(' ')}
      {...props}
    >
      {children}
    </button>
  );
};
